"use client";

import Link from "next/link";
import Image from "next/image";


const portfolioData = {
    sophia: {
        name: "Sophia Romero",
        role: "Concept Artist, Illustrator, and Animator",
        description: "Sophia specializes in early visual development...",
        images: ["/images/sophia_01.jpg", "/images/sophia_02.jpg", "/images/sophia_03.jpg"],
    },
    anjelika: {
        name: "Anjelika Benito",
        role: "Concept Artist, Illustrator, and Animator",
        description: "Anjelikaâ€™s portfolio highlights versatile artistic skills...",
        images: ["/images/anjelika_01.jpg", "/images/anjelika_02.jpg", "/images/anjelika_03.jpg"],
    },
    matthew: {
        name: "Matthew Castro",
        role: "3D Artist, 3D Environment, Video Editor",
        description: "Matthew's specialization is in designing and rendering full 3D environments...",
        images: ["/images/matthew_01.jpg", "/images/matthew_02.jpg", "/images/matthew_03.jpg"],
    },
    charie: {
        name: "Charie Tubil",
        role: "Content Developer",
        description: "Charie focuses on narrative and informational structure...",
        images: ["/images/charie_01.jpg", "/images/charie_02.jpg", "/images/charie_03.jpg"],
    },
    zelene: {
        name: "Zelene Plazuelo",
        role: "3D Artist, Illustrator, and Animator",
        description: "Zelene focuses on 3D modeling and texturing...",
        images: ["/images/zelene_01.jpg", "/images/zelene_02.jpg", "/images/zelene_03.jpg"],
    },
    allana: {
        name: "Allana Marcelo",
        role: "Concept Artist, Illustrator, and Animator",
        description: "Allana manages the bridge between initial concept art and final sequences.",
        images: ["/images/allana_01.jpg", "/images/allana_02.jpg", "/images/allana_03.jpg"],
    },
};

export default function PortfolioPage({ params }) {
    // normalize incoming id:
    const rawId = String(params?.id ?? "");
    const id = decodeURIComponent(rawId).trim().replace(/^\/+|\/+$/g, "").toLowerCase();

    const data = portfolioData[id];

    // Helpful debug + fallback if not found
    if (!data) {
        return (
            <main className="min-h-screen flex items-center justify-center p-6 bg-[#020202]">
                <div className="text-center">
                    <h1 className="text-2xl font-semibold text-white">Portfolio not found</h1>
                    <p className="text-gray-400 mt-2">
                        The member you requested does not exist.
                    </p>

                    <div className="mt-4 text-sm text-gray-300">
                        <div>Debug: params.id (raw) = <code className="bg-black/40 px-2 py-0.5 rounded">{rawId}</code></div>
                        <div>Debug: normalized id = <code className="bg-black/40 px-2 py-0.5 rounded">{id}</code></div>
                        <div className="mt-3">
                            <Link href="/#works" className="text-primary-purple hover:underline">
                                Back to Works
                            </Link>
                        </div>
                    </div>
                </div>
            </main>
        );
    }

    // Render portfolio when found
    return (
        <main className="py-16 px-6 bg-[#06070a] min-h-screen">
            <div className="max-w-4xl mx-auto bg-mid-gray/80 p-8 rounded-xl shadow-2xl">
                <div className="flex items-center justify-between border-b border-light-gray/20 pb-4 mb-8">
                    <Link href="/#works" className="px-4 py-2 bg-primary-purple/50 text-white rounded-full hover:bg-primary-purple transition text-sm">
                        &larr; Back to Works
                    </Link>
                    <h1 className="text-3xl font-extrabold text-white">Member Portfolio</h1>
                </div>

                <h2 className="text-4xl font-extrabold text-white mb-2">{data.name}</h2>
                <p className="text-lg text-primary-purple mb-8">{data.role}</p>

                <section className="mb-12">
                    <h3 className="text-2xl font-semibold text-white mb-4 border-b border-light-gray/20 pb-2">Description</h3>
                    <p className="text-gray-400 leading-relaxed">{data.description}</p>
                </section>

                <section className="mb-12">
                    <h3 className="text-2xl font-semibold text-white mb-6 border-b border-light-gray/20 pb-2">Member's Work</h3>
                    <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                        {data.images.map((src, idx) => (
                            <div key={idx} className="rounded overflow-hidden bg-black/40">
                                {/* images should be in /public/images/... */}
                                <Image
                                    src={src}
                                    alt={`${data.name} work ${idx + 1}`}
                                    width={800}
                                    height={600}
                                    className="object-cover w-full h-48"
                                />
                            </div>
                        ))}
                    </div>
                </section>

                <div className="text-center pt-4 text-gray-500 text-sm">End of Dedicated Portfolio View</div>
            </div>
        </main>
    );
}
